.more {
  background-color: #05040b;
  margin-top: -2px;
  padding-bottom: 30px;

  @include on-desktop {
    padding: 60px 0;
  }

  &__title {
    margin: 0;
    padding-top: 50px;

    @include on-tablet {
      text-align: center;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 21px;
    text-align: center;
    line-height: 25.41px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 21px;
    text-align: center;
    line-height: 25.41px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__subtitle {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 12px;
    text-align: left;
    line-height: 14.52px;
    color: #05c2df;
    margin-bottom: 40px;

    @include on-tablet {
      text-align: center;
    }

    @include on-desktop {
      font-size: 14px;
      line-height: 17px;
    }
  }

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__content-1 {
    grid-column: span 3;
    text-align: center;

    @include on-desktop {
      grid-column: 1 /4;
    }
  }

  &__content-2 {
    grid-column: 4 /-1;
    text-align: center;

    @include on-desktop {
      grid-column: 4 /7;
    }
  }

  &__content-3 {
    grid-column: span 3;
    text-align: center;

    @include on-desktop {
      grid-column: 7 /10;
    }
  }

  &__content-4 {
    grid-column: 4 /-1;
    text-align: center;

    @include on-desktop {
      grid-column: 10 /-1;
    }
  }

  &__photo {
    display: none;

    @include on-tablet {
      display: block;
    }
  }

  &__foto {
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__content {
    margin-bottom: 26px;

    @include on-tablet {
      margin-top: 20px;
    }

    @include on-desktop {
      margin-top: 50px;
    }
  }

  &__content-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 16px;
    text-align: left;
    line-height: 19.36px;
    color: #fff;

    @include on-tablet {
      text-align: center;
    }

    @include on-desktop {
      font-size: 18px;
      line-height: 21px;
    }
  }

  &__content-description-mb {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #929292;

    @include on-tablet {
      display: none;
    }
  }

  &__content-description-tab {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    line-height: 24px;
    color: #929292;
    display: none;

    @include on-tablet {
      display: block;
      width: 92%;
    }
  }
}
