.order {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 20px 0 30px;
  height: 100vh;
  pointer-events: none;
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  opacity: 0;
  transition: 0.3s;

  @include on-tablet {
    padding: 30px 0 40px;
  }

  @include on-desktop {
    padding: 20px 0 90px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__nav {
    position: relative;
    margin-bottom: 40px;

    @include on-desktop {
      display: flex;
      justify-content: space-between;
      margin: 40px 0 70px;
    }
  }

  &__nav-bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    margin-inline: -20px;

    @include on-tablet {
      margin-inline: -34px;
    }

    @include on-desktop {
      margin: 0;
      width: 80%;
    }
  }

  &__logo {
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__icon-close {
    position: absolute;
    right: 0;

    @include on-desktop {
      display: none;
    }
  }

  &__nav-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 15.6px;
    color: #545454;
    text-decoration: none;
    width: 33.6%;
    text-align: center;
    border-bottom: 1px solid #545454;
    padding-bottom: 16px;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
      border-bottom: 1px solid #05c2df;

      @include on-desktop {
        border: none;
      }
    }

    @include on-desktop {
      border: none;
    }
  }

  &__order-link {
    color: #05c2df;
    border-bottom: 1px solid #05c2df;

    @include on-desktop {
      border: none;
      position: relative;

      &::after {
        content: '';
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        left: 50%;
        top: 20px;
        width: 3px;
        height: 3px;
        border-radius: 2px;
        background-color: #05c2df;
      }
    }
  }

  &__content {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__photo-tab {
    display: none;

    @include on-tablet {
      display: block;
      text-align: center;
      transition: 0.3s;

      &:hover {
        transform: scale(1.02);
      }
    }

    @include on-desktop {
      display: none;
    }
  }

  &__photo-ds {
    display: none;

    @include on-desktop {
      display: block;
      grid-column: 1 /7;
      height: 280px;
      margin-left: -90px;
      padding-top: 90px;
      transition: 0.3s;

      &:hover {
        transform: scale(1.02);
      }
    }
  }

  &__foto-ds {
    @include on-desktop {
      width: 105%;
    }
  }

  &__product {
    display: flex;
    justify-content: space-around;

    @include on-tablet {
      width: 49%;
      justify-content: space-between;
    }

    @include on-desktop {
      grid-column: 1 /5;
      margin-top: -74px;
      width: 80%;
    }
  }

  &__label {
    display: block;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 20.8px;
    color: #929292;
  }

  &__price-label {
    text-align: center;
  }

  &__quantity-wrapper {
    margin-right: 16%;
  }

  &__quantity {
    display: flex;
    justify-content: center;
    position: relative;
    width: 90px;
    height: 40px;
    margin-top: 10px;

    &::after {
      content: '';
      background-image: url(../images/icons/select-after.svg);
      background-repeat: no-repeat;
      display: block;
      width: 9px;
      height: 6px;
      position: absolute;
      top: 16px;
      right: 12px;
    }
  }

  &__select {
    border: none;
    appearance: none;
    outline: none;
    width: 100%;
    text-align: center;
    background: #191536;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;
  }

  &__price {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 26px;
    text-align: left;
    line-height: 31.47px;
    color: #05c2df;
    display: block;
    margin-top: 10px;

    @include on-tablet {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__inputs-wrapper {
    padding: 20px 0;

    @include on-tablet {
      @include page-grid;

      padding: 20px 0 0;
    }

    @include on-desktop {
      display: flex;
      flex-wrap: wrap;
      grid-column: 7 /-1;
      grid-row: 1;
    }
  }

  &__input {
    width: 100%;
    height: 60px;
    background: transparent;
    color: #fff;
    border: none;
    outline: none;
    border-bottom: 1px solid #2f2f2f;
    cursor: pointer;
    transition: 0.3s;

    &:focus {
      border-bottom: 1px solid #05c2df;
    }

    @include on-tablet {
      height: 50px;
      margin-top: 27px;
    }

    @include on-desktop {
      width: 47%;
    }
  }

  &__labels {
    display: block;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    color: #929292;
  }

  &__selects {
    border: none;
    appearance: none;
    outline: none;
    width: 100%;
    height: 46px;
    padding-left: 20px;
    background: #191536;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;
  }

  &__country-wrapper {
    margin-top: 30px;

    @include on-tablet {
      grid-column: 1 /4;
      margin-top: 40px;
    }

    @include on-desktop {
      width: 47%;
    }
  }

  &__city-wrapper {
    margin-top: 30px;

    @include on-tablet {
      grid-column: 4 /-1;
      margin-top: 40px;
    }

    @include on-desktop {
      width: 48%;
    }
  }

  &__country,
  &__city {
    position: relative;
    margin-top: 10px;

    &::after {
      content: '';
      background-image: url(../images/icons/select-after.svg);
      background-repeat: no-repeat;
      display: block;
      width: 9px;
      height: 6px;
      position: absolute;
      top: 20px;
      right: 16px;
    }
  }

  &__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    background: #05c2df;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    text-decoration: none;
    border: none;
    outline: none;
    border-radius: 4px;
    color: #fff;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }

    @include on-tablet {
      grid-column: 2 /6;
      margin-top: 58px;
    }

    @include on-desktop {
      width: 220px;
    }
  }

  &__input-1 {
    @include on-tablet {
      grid-column: 1 /4;
    }
  }

  &__input-2 {
    @include on-tablet {
      grid-column: 4 /-1;
    }
  }

  &__input-3 {
    @include on-tablet {
      grid-column: 1 /4;
    }
  }

  &__input-4 {
    @include on-tablet {
      grid-column: 4 /-1;
    }
  }

  &__adress-1 {
    @include on-tablet {
      grid-column: 1 /4;
    }
  }

  &__adress-2 {
    @include on-tablet {
      grid-column: 4 /-1;
    }
  }
}
