.benefits {
  background: linear-gradient(146.86deg, #191536 -0.5%, #000 99.5%);
  padding: 40px 0;

  @include on-tablet {
    padding: 50px 0;
  }

  @include on-desktop {
    padding: 70px 0;
  }

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    @include on-tablet {
      grid-column: span 2;
      margin-bottom: 30px;
    }

    @include on-desktop {
      grid-column: 5 /9;
      letter-spacing: 2.2px;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 21px;
    text-align: left;
    line-height: 25.41px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 21px;
    text-align: left;
    line-height: 25.41px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 44px;
    }
  }

  &__content--1 {
    @include on-tablet {
      grid-column: 1 /3;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__content--2 {
    @include on-tablet {
      grid-column: 3 /5;
    }

    @include on-desktop {
      grid-column: 5 /9;
    }
  }

  &__content--3 {
    @include on-tablet {
      grid-column: 5 /-1;
    }

    @include on-desktop {
      grid-column: 9 /-1;
    }
  }

  &__content-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 18px;
    text-align: left;
    line-height: 21.78px;
    color: #fff;

    @include on-tablet {
      text-align: center;
      font-size: 14px;
      line-height: 19px;
      margin: 26px 0;
    }

    @include on-desktop {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__text {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    line-height: 24px;
    color: #929292;
  }

  &__text--mb {
    text-align: left;

    @include on-tablet {
      display: none;
    }
  }

  &__text--tab {
    display: none;

    @include on-tablet {
      display: block;
      margin-top: 24px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__text--ds {
    display: none;

    @include on-desktop {
      display: block;
      letter-spacing: 1.09px;
    }
  }

  &__icon {
    display: none;
    transition: 0.3s;

    @include on-tablet {
      display: block;
      margin: 0 auto;
    }

    &:hover {
      transform: scale(1.1);
    }
  }

  &__button {
    position: relative;
    width: 200px;
    height: 28px;
    border-radius: 4px;
    padding-top: 12px;
    background-color: #05c2df;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    line-height: 18.2px;
    color: #fff;
    text-decoration: none;
    margin: 30px 0 -46px;
    transition: 0.3s;
    display: none;

    @include on-desktop {
      display: block;
    }

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }
  }

  &__button-link {
    display: none;

    @include on-desktop {
      display: block;
      grid-column: 5 /9;
      margin: 90px auto;
    }
  }
}
