.tech {
  background-color: #05040b;
  padding: 40px 0;
  margin-top: -2px;

  @include on-tablet {
    padding: 70px 0;
  }

  @include on-desktop {
    padding: 86px 0;
  }

  &__title {
    margin-bottom: 34px;

    @include on-desktop {
      text-align: right;
      margin-right: 14%;
      margin-bottom: 80px;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 21px;
    text-align: left;
    line-height: 25.41px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 21px;
    text-align: left;
    line-height: 25.41px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__foto-mb {
    display: block;
    margin: 0 auto;
    transition: 0.3s;

    &:hover {
      transform: scale(1.03);
    }

    @include on-tablet {
      display: none;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      margin: 0 auto;
      transition: 0.3s;

      &:hover {
        transform: scale(1.03);
      }
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      margin: 0 auto;
      transition: 0.3s;

      &:hover {
        transform: scale(1.03);
      }
    }
  }
}
