.contacts {
  background: #05040b;
  padding: 46px 0;
  margin-top: -2px;

  @include on-tablet {
    padding: 60px 0 138px;
  }

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__titles {
    @include on-tablet {
      grid-column: 1 /3;
      grid-row: 1;
      margin-top: 20px;
    }

    @include on-desktop {
      grid-column: 1 /5;
    }
  }

  &__question {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    line-height: 16.94px;
    color: #05c2df;
  }

  &__title {
    margin-top: 10px;
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-size: 21px;
    font-weight: 900;
    line-height: 25.41px;
    text-align: left;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-size: 21px;
    font-weight: 400;
    line-height: 25.41px;
    text-align: left;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__links {
    display: none;

    @include on-tablet {
      display: block;
      grid-column: 1 /3;
      margin-top: -154px;
    }

    @include on-desktop {
      margin-top: -190px;
    }
  }

  &__link {
    display: block;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.36px;
    text-align: left;
    color: #929292;
    text-decoration: none;
    margin-bottom: 6px;
    transition: 0.3s;

    @include on-desktop {
      letter-spacing: 0.9px;
    }

    &:hover {
      color: #05c2df;
    }
  }

  &__inputs {
    position: relative;

    @include on-tablet {
      grid-column: 4 /-1;
      grid-row: 1;
    }

    @include on-desktop {
      grid-column: 7 /-1;
    }
  }

  &__label {
    font-family: Inter, sans-serif;
    color: transparent;
    font-size: 14px;
    transition: 0.3s;

    &:focus-within {
      color: #05c2df;
    }
  }

  &__input-wrapper {
    position: relative;
  }

  &__input {
    display: block;
    width: 100%;
    height: 60px;
    background: transparent;
    border: none;
    outline: none;
    margin-top: -16px;
    font-size: 16px;
    border-bottom: 1px solid #2f2f2f;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;

    &:focus {
      border-bottom: 1px solid #05c2df;
    }

    &::placeholder {
      font-size: 14px;
    }
  }

  &__input:-webkit-autofill {
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: #fff;
  }

  &__message {
    resize: none;
    border: none;
    outline: none;
    width: 100%;
    height: 100px;
    background: transparent;
    border-bottom: 1px solid #2f2f2f;
    line-height: 54px;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;

    &:focus {
      border-bottom: 1px solid #05c2df;
    }
  }

  &__button {
    display: block;
    width: 100%;
    height: 40px;
    background: #05c2df;
    border-radius: 4px;
    border: none;
    outline: none;
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 18.2px;
    color: #fff;
    margin: 30px 0 25px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }

    @include on-tablet {
      width: 200px;
      margin: 40px 0 0;
    }

    @include on-desktop {
      margin: 80px 0 0;
    }
  }

  &__arrow {
    background-image: url(../images/icons/contacts-arrow.svg);
    display: none;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 50px;
    height: 50px;
    transition: 0.3s;

    @include on-tablet {
      display: block;
    }

    &:hover {
      background-image: url(../images/icons/contacts-arrow-how.svg);
      transform: scale(1.2);
    }
  }

  &__text {
    font-family: Inter, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    text-align: left;
    color: #929292;

    @include on-tablet {
      grid-column: 1 /4;
      grid-row: 1;
      margin-top: 124px;
      font-size: 16px;
      line-height: 24px;
      height: 40px;
    }

    @include on-desktop {
      grid-column: 1 /5;
      letter-spacing: 1.1px;
    }
  }
}
