.menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 100vh;
  padding-top: 20px;
  pointer-events: none;
  opacity: 0;
  background: #191536;
  transition: 0.3s;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__close {
    text-align: right;
    margin-bottom: 38px;
  }

  &__links {
    align-content: center;
    height: 42px;
    margin-inline: -20px;
    transition: 0.3s;

    &:hover {
      background: #110e25;
    }

    @include on-tablet {
      margin-inline: -34px;
      padding-left: 140px;
    }
  }

  &__link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 21px;
    text-align: left;
    line-height: 27.3px;
    padding: 8px 20px;
    color: #fff;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
    }

    @include on-tablet {
      padding: 8px 34px;
    }
  }
}
