.complete {
  position: fixed;
  height: 100vh;
  top: 0;
  right: 0;
  left: 0;
  pointer-events: none;
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  opacity: 0;
  padding: 20px 0 180px;
  transition: 0.3s;

  @include on-tablet {
    padding: 30px 0 420px;
  }

  @include on-desktop {
    padding-bottom: 230px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__nav {
    position: relative;
    margin-bottom: 40px;

    @include on-desktop {
      display: flex;
      justify-content: space-between;
      margin: 40px 0 70px;
    }
  }

  &__nav-bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    margin-inline: -20px;

    @include on-tablet {
      margin-inline: -34px;
    }

    @include on-desktop {
      margin: 0;
      width: 80%;
    }
  }

  &__logo {
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__icon-close {
    position: absolute;
    right: 0;

    @include on-desktop {
      display: none;
    }
  }

  &__nav-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 15.6px;
    color: #545454;
    text-decoration: none;
    width: 33.6%;
    text-align: center;
    border-bottom: 1px solid #545454;
    padding-bottom: 16px;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
    }

    @include on-desktop {
      border: none;
    }
  }

  &__complete-link {
    color: #05c2df;
    border-bottom: 1px solid #05c2df;

    @include on-desktop {
      border: none;
      position: relative;

      &::after {
        content: '';
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        left: 50%;
        top: 20px;
        width: 3px;
        height: 3px;
        border-radius: 2px;
        background-color: #05c2df;
      }
    }
  }

  &__title {
    margin-top: 76px;
    font-family: Inter, sans-serif;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 26px;
    text-align: center;
    line-height: 33.8px;
    color: #fff;

    @include on-tablet {
      font-size: 46px;
      line-height: 60px;
      margin-top: 180px;
    }

    @include on-desktop {
      margin-top: 140px;
    }
  }

  &__colored-title {
    font-weight: 400;
    color: #05c2df;
  }

  &__text {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    line-height: 20.8px;
    color: #fff;
  }

  &__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    background: #05c2df;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    text-decoration: none;
    border: none;
    outline: none;
    border-radius: 4px;
    color: #fff;
    margin-top: 40px;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }

    @include on-tablet {
      width: 80%;
      height: 48px;
      margin: 40px auto;
    }

    @include on-desktop {
      width: 220px;
    }
  }
}
