.header {
  padding: 20px 0 28px;
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }

    @include on-desktop {
      margin-bottom: 40px;
    }
  }

  &__top-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__logo-mb {
    @include on-tablet {
      display: none;
    }
  }

  &__logo-tab {
    display: none;

    @include on-tablet {
      display: block;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__logo-ds {
    display: none;

    @include on-desktop {
      display: block;
    }
  }

  &__select {
    display: none;

    @include on-desktop {
      display: inline-block;
    }

    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 20.8px;
    color: #05c2df;
    margin-left: -44%;
    cursor: pointer;

    &::after {
      content: '';
      background-image: url(../images/icons/select-after.svg);
      background-repeat: no-repeat;
      display: inline-block;
      width: 9px;
      height: 6px;
    }
  }

  &__links {
    display: none;

    @include on-desktop {
      display: inline-block;
    }
  }

  &__link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 20.8px;
    text-decoration: none;
    margin-right: 20px;
    color: #fff;
    transition: 0.3s;

    &:hover {
      color: #929292;
      border-bottom: 1px solid #05c2df;
      padding-bottom: 3px;
    }
  }

  &__nav-button {
    display: inline-block;
    width: 200px;
    height: 34px;
    border-radius: 4px;
    background-color: #05c2df;
    padding-top: 12px;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    line-height: 20.8px;
    color: #fff;
    text-decoration: none;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }
  }

  &__menu-link {
    position: relative;

    @include on-desktop {
      display: none;
    }
  }

  &__content {
    @include on-tablet {
      grid-column: span 3;
      grid-row: 1;
      width: 85%;
    }

    @include on-desktop {
      grid-column: 1 /6;
      width: 100%;
    }
  }

  &__photo {
    margin-top: 14px;

    @include on-tablet {
      grid-column: 4 /-1;
      margin: -38px 0 0 -100px;
    }

    @include on-desktop {
      grid-column: 6 /-1;
      position: relative;
      top: 40px;
      left: 60px;
    }
  }

  &__foto-mb {
    width: 100%;

    @include on-tablet {
      display: none;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      width: 100%;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
    }
  }

  &__title {
    margin-top: 0;

    @include on-tablet {
      margin-top: 50px;
    }

    @include on-desktop {
      margin-top: 90px;
      width: 560px;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 25px;
    text-align: left;
    line-height: 33.8px;
    color: #fff;

    @include on-desktop {
      font-size: 46px;
      line-height: 60px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 26px;
    text-align: left;
    line-height: 33.8px;
    color: #05c2df;

    @include on-desktop {
      font-size: 46px;
      line-height: 60px;
    }
  }

  &__description {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #929292;

    @include on-desktop {
      margin-inline: 90px;
    }
  }

  &__price {
    display: block;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 18px;
    text-align: center;
    line-height: 21.78px;
    color: #fff;

    @include on-tablet {
      text-align: left;
    }

    @include on-desktop {
      margin-left: 90px;
    }
  }

  &__play {
    background-image: url(../images/header/play-button-mb.png);
    background-repeat: no-repeat;
    width: 210px;
    height: 70px;
    display: block;

    &:hover {
      background-image: url(../images/header/play-hover.png);
    }

    @include on-tablet {
      grid-column: 1 /3;
      margin-top: -42%;
    }

    @include on-desktop {
      margin: -160px 0 0 90px;
    }
  }

  &__button {
    position: relative;
    display: block;
    width: 100%;
    height: 28px;
    border-radius: 4px;
    padding-top: 12px;
    background-color: #05c2df;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    line-height: 18.2px;
    color: #fff;
    text-decoration: none;
    margin: 30px 0 -46px;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }

    @include on-tablet {
      width: 450px;
      margin: 0 auto -46px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__bottom-nav {
    display: none;

    @include on-desktop {
      display: flex;
      justify-content: space-between;
      margin: 20px 0;
    }
  }

  &__bottom-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #fff;
    text-decoration: none;
    margin-right: 20px;
    transition: 0.3s;

    &:hover {
      color: #929292;
      border-bottom: 1px solid #05c2df;
      padding-bottom: 3px;
    }
  }

  &__link-more {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #05c2df;
    text-decoration: none;

    &::after {
      content: '';
      background-image: url(../images/icons/select-after.svg);
      background-repeat: no-repeat;
      display: block;
      width: 9px;
      height: 6px;
      margin: 8px 14px;
    }

    &:hover {
      color: #fff;

      &::after {
        background-image: url(../images/icons/select-after-hov.svg);
      }
    }
  }

  &__slider {
    &::after {
      content: '';
      display: block;
      width: 150px;
      height: 1px;
      background-color: #212121;
      margin-top: 6px;
    }
  }

  &__slider-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #fff;
    text-decoration: none;

    &:hover {
      color: #929292;
      border-bottom: 1px solid #05c2df;
      padding-bottom: 10px;
    }
  }

  &__step-next {
    margin-left: 50px;
  }
}
