.footer {
  padding: 46px 0;
  background-color: #000;
  margin-top: -2px;

  @include on-tablet {
    padding: 22px 0;
  }

  @include on-desktop {
    padding: 40px 0;
  }

  &__wrapper {
    @include page-grid;

    @include on-tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__logo {
    grid-column: 1 /2;
    margin-bottom: 18px;

    @include on-tablet {
      margin: 0;
    }
  }

  &__links {
    grid-column: 1 /2;
  }

  &__link {
    display: block;
    font-family: Inter, sans-serif;
    font-size: 13px;
    line-height: 19.5px;
    font-weight: 400;
    color: #fff;
    text-align: left;
    text-decoration: none;
    margin-bottom: 18px;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
    }

    @include on-tablet {
      display: inline;
      margin: 0 20px;
    }

    @include on-desktop {
      font-size: 14px;
      line-height: 21px;
      margin: 0 30px;
    }
  }

  &__socials {
    grid-column: 2 /-1;
    margin-top: -36px;

    @include on-tablet {
      margin: 0;
    }
  }

  &__social {
    text-decoration: none;
  }

  &__social:not(:last-child) {
    margin-right: 20px;
  }

  &__social-icon {
    transition: 0.3s;

    &:hover {
      transform: scale(1.3);
    }
  }

  &__contacts {
    grid-column: 2 /-1;

    @include on-tablet {
      display: none;
    }
  }

  &__contact {
    display: block;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 13px;
    text-align: left;
    line-height: 15.73px;
    color: #929292;
    text-decoration: none;
    margin-bottom: 20px;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
    }
  }
}
