.pay {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  padding: 20px 0 30px;
  height: 100vh;
  pointer-events: none;
  background: linear-gradient(107.56deg, #191536 0%, #000 100%);
  opacity: 0;
  transition: 0.3s;

  @include on-tablet {
    padding: 30px 0 40px;
  }

  @include on-desktop {
    padding: 20px 0 98px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  &__nav {
    position: relative;
    margin-bottom: 40px;

    @include on-desktop {
      display: flex;
      justify-content: space-between;
      margin: 40px 0 70px;
    }
  }

  &__nav-bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    margin-inline: -20px;

    @include on-tablet {
      margin-inline: -34px;
    }

    @include on-desktop {
      margin: 0;
      width: 80%;
    }
  }

  &__logo {
    text-decoration: none;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__icon-close {
    position: absolute;
    right: 0;

    @include on-desktop {
      display: none;
    }
  }

  &__nav-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 15.6px;
    color: #545454;
    text-decoration: none;
    width: 33.6%;
    text-align: center;
    border-bottom: 1px solid #545454;
    padding-bottom: 16px;
    transition: 0.3s;

    &:hover {
      color: #05c2df;
    }

    @include on-desktop {
      border: none;
    }
  }

  &__order-link {
    color: #fff;
  }

  &__pay-link {
    color: #05c2df;
    border-bottom: 1px solid #05c2df;

    @include on-desktop {
      border: none;
      position: relative;

      &::after {
        content: '';
        background-repeat: no-repeat;
        display: block;
        position: absolute;
        left: 50%;
        top: 20px;
        width: 3px;
        height: 3px;
        border-radius: 2px;
        background-color: #05c2df;
      }
    }
  }

  &__content {
    @include on-desktop {
      @include page-grid;
    }
  }

  &__photo-tab {
    display: none;

    @include on-tablet {
      display: block;
      text-align: center;
      transition: 0.3s;

      &:hover {
        transform: scale(1.02);
      }
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-tab {
    margin-left: -5%;
  }

  &__photo-ds {
    display: none;

    @include on-desktop {
      display: block;
      grid-column: 1 /7;
      height: 280px;
      margin-left: -90px;
      padding-top: 62px;
      transition: 0.3s;

      &:hover {
        transform: scale(1.02);
      }
    }
  }

  &__foto-ds {
    @include on-desktop {
      width: 105%;
    }
  }

  &__product {
    display: flex;
    justify-content: space-around;

    @include on-tablet {
      justify-content: flex-start;
      width: 65%;
      margin: 0 auto;
    }

    @include on-desktop {
      grid-column: 1 /5;
      margin-top: -74px;
      width: 100%;
    }
  }

  &__price-label {
    text-align: center;
  }

  &__quantity-wrapper {
    margin-right: 16%;

    @include on-tablet {
      margin-right: 36%;
    }

    @include on-desktop {
      margin-right: 30%;
    }
  }

  &__quantity {
    display: flex;
    justify-content: center;
    position: relative;
    width: 90px;
    height: 40px;
    margin-top: 10px;

    &::after {
      content: '';
      background-image: url(../images/icons/select-after.svg);
      background-repeat: no-repeat;
      display: block;
      width: 9px;
      height: 6px;
      position: absolute;
      top: 16px;
      right: 12px;
    }
  }

  &__select {
    border: none;
    appearance: none;
    outline: none;
    width: 100%;
    text-align: center;
    background: #191536;
    color: #fff;
    cursor: pointer;
    transition: 0.3s;
  }

  &__price {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 26px;
    text-align: left;
    line-height: 31.47px;
    color: #05c2df;
    display: block;
    margin-top: 10px;

    @include on-tablet {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__inputs-wrapper {
    margin-top: 50px;

    @include on-tablet {
      width: 65%;
      margin: 60px auto 0;
    }

    @include on-desktop {
      grid-column: 7 /12;
      grid-row: 1;
      width: 100%;
    }
  }

  &__wrap {
    display: flex;
    gap: 4%;

    @include on-tablet {
      gap: 25px;
    }
  }

  &__input {
    border: none;
    border-bottom: 1px solid #2f2f2f;
    outline: none;
    background: transparent;
    color: #fff;
    font-family: Inter, sans-serif;
    cursor: pointer;
    transition: 0.3s;

    &:focus {
      border-bottom: 1px solid #05c2df;
    }
  }

  &__number-input {
    width: 22%;
    margin-top: 14px;
    font-weight: 400;
    font-size: 19px;
    letter-spacing: 0.3em;
    line-height: 26px;
    text-align: left;

    &::placeholder {
      color: #2f2f2f;
    }

    @include on-tablet {
      width: 16%;
      font-size: 21px;
    }
  }

  &__name-input {
    height: 40px;
    width: 100%;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    margin-top: 16px;

    @include on-tablet {
      height: 58px;
    }
  }

  &__card-data-inputs {
    display: flex;
    margin-top: 30px;

    @include on-tablet {
      margin-top: 40px;
    }
  }

  &__date {
    margin-right: 18%;

    @include on-desktop {
      margin-right: 20%;
    }
  }

  &__label-card-exp {
    display: flex;
    flex-direction: column;
  }

  &__card-exp-input {
    width: 90px;
    height: 40px;
    margin-top: 16px;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 18px;
    text-align: left;
    line-height: 23.4px;

    &::placeholder {
      color: #2f2f2f;
    }
  }

  &__label-cvv {
    display: flex;
    flex-direction: column;
  }

  &__cvv-input {
    width: 42px;
    height: 40px;
    margin-top: 16px;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 0.2em;
    line-height: 23.4px;
    text-align: left;

    &::placeholder {
      color: #2f2f2f;
    }

    @include on-tablet {
      font-size: 20px;
    }
  }

  &__card-icon {
    display: none;

    @include on-tablet {
      display: block;
      position: absolute;
      right: 0;
      bottom: 0;
      cursor: pointer;
      transition: 0.3s;

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  &__label {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    color: #929292;
    transition: 0.3s;

    &:focus-within {
      color: #05c2df;
    }
  }

  &__card-number-label {
    position: relative;
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    color: #929292;

    &:focus-within {
      color: #05c2df;
    }
  }

  &__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    background: #05c2df;
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    line-height: 18.2px;
    text-decoration: none;
    border: none;
    outline: none;
    border-radius: 4px;
    color: #fff;
    margin-top: 40px;
    transition: 0.3s;

    &:hover {
      background-color: #fff;
      color: #05c2df;
    }

    @include on-tablet {
      margin-top: 100px;
    }

    @include on-desktop {
      width: 210px;
    }
  }
}
