.questions {
  position: fixed;
  top: 0;
  right: 0;
  pointer-events: none;
  height: 100vh;
  opacity: 0;
  transition: 0.3s;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  @include on-desktop {
    background: #000000b2;
  }

  &__wrapper {
    padding: 20px 0;
    background: #191536;

    @include on-tablet {
      padding-bottom: 250px;
    }

    @include on-desktop {
      margin: 90px 165px;
      padding: 20px 60px 50px;
    }
  }

  &__close {
    text-align: right;

    @include on-desktop {
      margin-right: -90px;
    }
  }

  &__title {
    @include on-tablet {
      width: 250px;
    }

    @include on-desktop {
      width: 450px;
    }
  }

  &__title-bold {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 21px;
    text-align: left;
    line-height: 27px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 47px;
    }
  }

  &__title-colored {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 21px;
    text-align: left;
    line-height: 27px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 47px;
    }
  }

  &__question {
    border: 1px solid #05c2df;
    padding-inline: 8px;
    margin-top: 10px;

    @include on-tablet {
      padding-inline: 20px;
      margin-top: 14px;
    }

    @include on-desktop {
      padding-bottom: 12px;
    }
  }

  &__question-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #05c2df;
    padding-right: 20px;
    cursor: pointer;
  }

  &__question-title--1 {
    &::after {
      content: '';
      background-image: url(../images/icons/after-down.svg);
      background-repeat: no-repeat;
      width: 9px;
      height: 6px;
      margin-left: 100%;
      margin-top: -20px;
      cursor: pointer;

      @include on-desktop {
        display: block;
      }
    }
  }

  &__question-title--2,
  &__question-title--3,
  &__question-title--4 {
    &::after {
      content: '';
      background-image: url(../images/icons/after-right.svg);
      background-repeat: no-repeat;
      width: 9px;
      height: 6px;
      margin-left: 100%;
      margin-top: -18px;
      cursor: pointer;

      @include on-desktop {
        display: block;
      }
    }
  }

  &__text {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    letter-spacing: 0.8px;
    color: #929292;

    @include on-tablet {
      letter-spacing: 0.6px;
    }
  }

  &__updating {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 12px;
    text-align: left;
    line-height: 18px;
    color: #545454;
    padding-right: 20px;
  }

  &__link {
    display: none;

    @include on-tablet {
      display: block;
      text-align: center;
      margin-top: 55px;
    }
  }
}
