.about {
  background: linear-gradient(146.86deg, #191536 -0.5%, #000 99.5%);
  margin-top: -21px;
  padding: 30px 0 60px;

  @include on-desktop {
    padding: 50px 0 90px;
  }

  &__wrapper {
    @include on-tablet {
      @include page-grid;
    }
  }

  &__title {
    @include on-tablet {
      grid-column: 4 /-1;
    }

    @include on-desktop {
      grid-column: 9 /-1;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 21px;
    text-align: left;
    line-height: 25px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 21px;
    text-align: left;
    line-height: 25px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 43px;
    }
  }

  &__hello {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 14px;
    text-align: left;
    line-height: 16.94px;
    color: #05c2df;
  }

  &__content-2-title {
    margin-top: 6px;

    @include on-tablet {
      margin-bottom: 24px;
    }
  }

  &__text--1,
  &__text--2 {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #929292;

    @include on-tablet {
      letter-spacing: 0.5px;
    }

    @include on-desktop {
      letter-spacing: 1.1px;
    }
  }

  &__photo {
    transition: 0.3s;

    @include on-tablet {
      grid-column: span 3;
      margin-top: -40px;
    }

    @include on-desktop {
      grid-column: 3 /8;
      margin-top: -50px;

      &::before {
        content: '';
        background-image: url(../images/about/before.svg);
        background-repeat: no-repeat;
        display: block;
        width: 21px;
        height: 17px;
        margin-left: -40px;
        margin-bottom: -20px;
      }
    }
  }

  &__foto-mb {
    width: 100%;
    transition: 0.3s;

    &:hover {
      transform: scale(1.03);
    }

    @include on-tablet {
      display: none;
    }
  }

  &__foto-tab {
    display: none;

    @include on-tablet {
      display: block;
      transition: 0.3s;

      &:hover {
        transform: scale(1.03);
      }
    }

    @include on-desktop {
      display: none;
    }
  }

  &__foto-ds {
    display: none;

    @include on-desktop {
      display: block;
      width: 100%;
      transition: 0.3s;

      &:hover {
        transform: scale(1.03);
      }
    }
  }

  &__dotts {
    display: block;
    text-align: center;

    @include on-desktop {
      display: none;
    }
  }

  &__slider {
    display: none;

    @include on-desktop {
      display: block;
      margin-top: 10px;
    }

    &::after {
      content: '';
      display: block;
      width: 150px;
      height: 1px;
      background-color: #212121;
      margin-top: 6px;
    }
  }

  &__slider-link {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    color: #fff;
    text-decoration: none;
    margin-right: 50px;

    &:hover {
      color: #929292;
      border-bottom: 1px solid #05c2df;
      padding-bottom: 10px;
    }
  }

  &__play-button {
    background-image: url(../images/header/play-button-mb.png);
    background-repeat: no-repeat;
    width: 210px;
    height: 70px;
    display: block;
    margin: 50px 0 80px;

    &:hover {
      background-image: url(../images/header/play-hover.png);
    }

    @include on-tablet {
      margin-top: 30px;
    }

    @include on-desktop {
      margin-top: 60px;
    }
  }

  &__content--1 {
    @include on-tablet {
      grid-column: 4 /-1;
    }

    @include on-desktop {
      grid-column: 9 /-1;
    }
  }

  &__content--2 {
    @include on-tablet {
      grid-column: span 4;
      margin-top: 44px;
    }

    @include on-desktop {
      grid-column: span 5;
      margin-top: 174px;
    }
  }

  &__logos-tab {
    display: none;

    @include on-tablet {
      display: block;
      grid-column: 5 /-1;
      margin-top: 22px;
    }

    @include on-desktop {
      display: none;
    }
  }

  &__logo--tab-1 {
    background-image: url(../images/about/tab-1.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 42px;
    height: 253px;
    margin-right: 40px;
  }

  &__logo--tab-2 {
    background-image: url(../images/about/tab-2.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 42px;
    height: 253px;
    margin-right: 40px;
  }

  &__logo--tab-3 {
    background-image: url(../images/about/tab-3.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 42px;
    height: 253px;
  }

  &__logos-ds {
    display: none;

    @include on-desktop {
      display: block;
      grid-column: 7 /-1;
      margin-top: 140px;
    }
  }

  &__logo--ds-1 {
    background-image: url(../images/about/ds-1.svg);
    background-repeat: no-repeat;
    display: inline-block;
    height: 337px;
    width: 80px;
    margin-right: 30px;
    margin-left: 28px;
  }

  &__logo--ds-2 {
    background-image: url(../images/about/ds-2.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 80px;
    height: 337px;
    margin-right: 30px;
  }

  &__logo--ds-3 {
    background-image: url(../images/about/ds-3.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 80px;
    height: 337px;
    margin-right: 30px;
  }

  &__logo--ds-4 {
    background-image: url(../images/about/ds-4.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 80px;
    height: 337px;
    margin-right: 30px;
  }

  &__logo--ds-5 {
    background-image: url(../images/about/ds-5.svg);
    background-repeat: no-repeat;
    display: inline-block;
    width: 80px;
    height: 337px;
  }
}
