.help {
  position: fixed;
  top: 0;
  right: 0;
  pointer-events: none;
  height: 100vh;
  opacity: 0;
  transition: 0.3s;

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  @include on-tablet {
    background: #000000b2;
  }

  &__wrapper {
    padding: 20px 0 80px;
    background: #191536;

    @include on-tablet {
      margin-bottom: 200px;
      padding: 20px 0 110px;
    }

    @include on-desktop {
      margin: 90px 165px;
      padding: 20px 60px 50px;
    }
  }

  &__close {
    text-align: right;

    @include on-desktop {
      margin-right: -90px;
    }
  }

  &__title {
    margin: 24px 0;

    @include on-tablet {
      margin: 34px 0;
    }
  }

  &__bold-title {
    font-family: Inter, sans-serif;
    font-weight: 900;
    font-size: 20px;
    text-align: left;
    line-height: 27.3px;
    color: #fff;

    @include on-desktop {
      font-size: 36px;
      line-height: 47px;
    }
  }

  &__colored-title {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 20px;
    text-align: left;
    line-height: 27.3px;
    color: #05c2df;

    @include on-desktop {
      font-size: 36px;
      line-height: 47px;
    }
  }

  &__text {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #929292;
    margin: 24px 0;
  }

  &__links-blocks-wrapper {
    @include on-tablet {
      display: flex;
    }
  }

  &__links {
    margin: 34px 0;
  }

  &__text-link {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    text-decoration: none;
    color: #05c2df;
  }

  &__link-wrapper {
    margin: 20px 0;
  }

  &__link {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 24px;
    text-decoration: none;
    color: #05c2df;
  }

  &__contacts-block {
    @include on-tablet {
      margin: 16px 0 0 164px;
    }
  }

  &__contacts-wrapper {
    margin: 14px 0;

    @include on-tablet {
      margin: 24px 0;
    }
  }

  &__contact {
    font-family: Inter, sans-serif;
    font-weight: 400;
    font-size: 16px;
    text-align: left;
    line-height: 19.36px;
    text-decoration: none;
    color: #929292;
  }
}
